import { noop } from 'lodash-es';
import React from 'react';

interface Props {
  src?: string;
  position: number;
  onMouseDown?: (e: React.MouseEvent) => void;
}

export const PositionedImage = React.forwardRef<HTMLImageElement, Props>(
  (props: Props, ref) => {
    const { src, position, onMouseDown = noop } = props;

    // 禁止拖拽图片
    const onDragStart = (e: React.MouseEvent) => {
      e.preventDefault();
    };

    return (
      <img
        ref={ref}
        src={src}
        width="100%"
        height="100%"
        style={{
          objectFit: 'cover',
          objectPosition: `center ${position * 100}%`,
        }}
        onMouseDown={onMouseDown}
        onDragStart={onDragStart}
      />
    );
  },
);
